import React from 'react';
import { Link } from 'react-router-dom';

const PostList = ({ posts }) => (
  <>
    {posts.map((post, key) => (
      <Link className="article-list-item" key={key} to={`/post/${post.name}`}>
        <h3>{post.title}</h3>
        <p>{post.content[0].substring(0, 150)}...</p>
      </Link>
    ))}
  </>
);

export default PostList;
