import React, { useState, useEffect } from 'react';

import posts from './posts';
import PostList from '../components/PostList';
import CommentList from '../components/CommentList';
import Likes from '../components/Likes';
import CommentForm from '../components/CommentForm';

const PostPage = ({ match }) => {
  const name = match.params.name;
  const post = posts.find(post => post.name === name);

  const [postDetails, setPostDetails] = useState({ likes: 0, comments: [] });

  useEffect(() => {
    const getData = async () => {
      const result = await fetch(`/api/posts/${name}`);
      const body = await result.json();
      setPostDetails(body);
    };
    getData();
  }, [name]);

  if (!post) return <h1>OPS! This post does not exit.</h1>;

  const related = posts.filter(post => post.name !== name);

  return (
    <>
      <h1>{post.title}</h1>
      <Likes likes={postDetails.likes} postName={name} setPost={setPostDetails} />
      {post.content.map((p, key) => (
        <p key={key}>{p}</p>
      ))}
      <CommentForm postName={name} setPost={setPostDetails} />
      <CommentList comments={postDetails.comments} />
      <h3>Related Posts</h3>
      <PostList posts={related} />
    </>
  );
};

export default PostPage;
