import React from 'react';

const Likes = ({ postName, likes, setPost }) => {
  const likePost = async () => {
    const result = await fetch(`/api/posts/${postName}/like`, {
      method: 'post'
    });
    const body = await result.json();
    setPost(body);
  };

  return (
    <div className="upvotes-section">
      <p>This post has won {likes} claps</p>
      <button onClick={() => likePost()}>Be grateful <span role="img" aria-label="clap">üëè</span></button>
    </div>
  )
};

export default Likes;
