import React, { useState } from 'react';

const CommentForm = ({ postName, setPost }) => {
  const [username, setUserName] = useState('');
  const [comment, setComment] = useState('');

  const createComment = async () => {
    const result = await fetch(`/api/posts/${postName}/comment`, {
      method: 'post',
      body: JSON.stringify({ username, comment }),
      headers: {
        'Content-Type': 'application/json'
      }
    });
    const body = await result.json();
    setPost(body);
  }

  return (
    <div id="add-comment-form">
      <h3>Add comment:</h3>
      <label>
        Name:
        <input type="text" value={username} onChange={(event) => setUserName(event.target.value)} />
      </label>
      <label>
        Comment:
        <textarea rows="4" cols="50" value={comment} onChange={(event) => setComment(event.target.value)} />
      </label>
      <button onClick={() => createComment()}>Add comment</button>
    </div>
  )
}

export default CommentForm
